.ba-footer{
    
}
.ba-contacts{
    padding-top: 50px;
    padding-right: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.ba-footer-contacts{
    background: $light-gray;
   
}
.ba-contacts{
    font-size: 14px;
    text-align:center;
    &__title{
        @include breakpoint(medium){
            font-size: 24px;
            font-weight: 100;
        }
    }
    &__city{
        @include breakpoint(medium){
            font-size: 30px;
            font-weight: 100;
        }
    }
    a{
        color: $body-font-color;
        
        &:hover{
            color: $dark-yellow;
        }
        @include breakpoint(medium){
            font-size: 18px;
            
        }
    }
}
.ba-footer-socials{
    background: $light-yellow;
    padding: 20px 30px 5px 30px;
    &--right{
        display: flex;
        justify-content: space-between;
        padding-bottom: 20px;
    }
    &--left{
        display: flex;
        justify-content: center;
    }
    a{
        color: $dark-yellow;
        &:hover, &:focus,svg{
            color: #725419;
        }
    }
    
}
.footer-photo{
    padding-left: 0;
}

.footer-button{
    color: $dark-gray;
    background-color: $light-yellow;
    border: 1px solid $dark-yellow;
}