.ba-about{
    display:flex;
    justify-content: center;
    align-items: center;
    background-color: $light-gray;
    z-index:-1;
    @include breakpoint(small){
        margin-left: 20px;
        margin-right: 20px;
        display: inline;
        text-align: center;
    }
    @include breakpoint(medium){
        display:flex;
        text-align: left;
        padding:20px 15px 40px;
    }
    @include breakpoint(large){
        padding:59px 50px 80px 135px;
        
    }
    &__photo{
        padding-top: 35px;
    }
    &__text{
        padding-top: 20px;
        font-size: 12px;
        font-weight: 300;
        @include breakpoint(medium){
            vertical-align: middle;
            font-size: 14px;
        }
        span{
            letter-spacing: 0.04em;
        }
        p{
            line-height: 1.5;
            letter-spacing: 0.0414em;
            padding-top: 15px;
            text-align: left;
            @include breakpoint(medium){
                padding-top: 10px;
                font-size: 14px;
                
            }
            
        }
    }
    
}