.ba-intro{
	background: #fff url('../img/bg_intro.jpg') 100% no-repeat ;
	background-size: cover;
	box-shadow: 0px 7px 10px 0px rgba(0, 0, 0, 0.35);
	padding-bottom: 85px;
	z-index: 2;
	@include breakpoint(small down){
		padding-bottom: 20px;
	}
}
.ba-header{
	padding-top: 30px;
	@include breakpoint(medium){
		padding: 25px;
		margin-bottom: 45px;
		
	}
	@include breakpoint(medium){
		background-color: rgba(0, 0, 0, 0.45);
		
	}
}
.ba-main-menu{
	
	& > li + li{
		margin-left: 10px;
	}
	a{
		font-size: 14px;
		font-weight: 400;
		letter-spacing: 0.014em;
		color: $white;
		&:hover, &:focus{
			color:$primary-color;
		}
	}
}
.ba-logo{
	margin: 0 auto;
	display:block;
	border-radius: 50%;
	display:flex;
	justify-content: center;
	align-items:center;
	background: $white;
	opacity: 1;
	width: 300px;
	height: 300px;
	@include breakpoint(small down){
		width: 109px;
		height: 109px;
		opacity: 0.7;
		
	}
	img{
		@include breakpoint(small down){
			width: 104px;
			height: 104px;
		 }
	}
	
}
.ba-intro-logo{
	color: $white;
	margin: 0 auto;
	// padding-top: 40px;
	a{
		margin: 0 auto;
		display: block;
		max-width: 300px;
		max-height: 300px;
		border-radius: 50%;

	}
}
.ba-intro-title{
	font-size: 36px;
	font-weight: 400;
	text-align: center;
	padding-top: 40px;
}