.ba-clients{
    padding-top: 30px;
    // padding-bottom: 10px;
    background: $light-gray;
    @include breakpoint(small){
      padding-top: 30px;
    }
    @include breakpoint(large){
      padding-top: 40px;
    }
    &__title{
        display: block;
        margin: 0 auto;
        text-align: center;
        @include breakpoint(small){
          padding-bottom: 30px;
        }
        @include breakpoint(medium){
          padding-bottom: 20px;
        }
    }
}

.ba-testemonilas{
    // padding-top: 30px;
    max-width: 800px;
    margin: 0 auto;
    &__slide{
        padding: 20px;
        text-align: center;
    }
  }

  .ba-people-section{
    position: relative;
    z-index: 2;
    @include breakpoint(medium){
      box-shadow: 0px 7px 10px 0px rgba(0, 0, 0, 0.35);
      background: $light-yellow;
      
    }
  }
  .ba-people{
    padding: 0 0px;
    background: $light-yellow;
    @include breakpoint(small down){
      background: $light-gray;
    }
    &__slide{
      // position: relative;
      text-align: center;
      padding: 35px;
      cursor: pointer;
     
      &.slick-current{
        background: $light-gray;
        @include breakpoint(medium){
          box-shadow: inset 0px 0px 3px 1px rgba(0, 0, 0, 0.35);
          
        }
      }
      img{
        @include breakpoint(small down){
          width: 70%;
          margin: 0 auto;
          margin-bottom: 15px;
        }
        width: 100%;
        height: auto;
        margin-bottom: 20px;
        
      }
    }
  }
  .ba-testemonilas-small{
    text-align: left;
    // border-bottom: 1px solid $dark-gray;
    // margin-top: -30px;
    position: relative;
    &:before{
      content: '';
      display: block;
      position: absolute;
      width: 170px;
      height: 2px;
      background-color: $dark-gray;
      bottom: 5px;
      left: 50px;
    }
    @include breakpoint(small down){
      font-size: 12px;
      background: $light-gray;
    }
  }

.ba-clients, .ba-people-section {
  @include breakpoint(small down){
    
    margin-left: 10px;
    margin-right: 10px;
  }
}

