.ba-product{
    background-color: $light-yellow;
    box-shadow: 0px 7px 10px 0px rgba(0, 0, 0, 0.35);
    text-align: center;
    padding-top: 25px;
    position: relative;
    z-index: 99;
    @include breakpoint(medium){
        background-color: $white;
        padding-top: 64px;    
        }
    &__title{
        font-size: 14px;
        margin-bottom: 35px;
        @include breakpoint(medium){
            margin-bottom: 50px;
            font-size: 18px;
        }
    }
    &-item{
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        margin-bottom: 30px;
        
        ul{
            flex-grow: 1;
            margin-left: -20px;
        }
        li{
            list-style: none;
        }
        img{
            padding-bottom: 15px;
        }
    }
}
.button{
    color:$black;
    font-size: 16px;
    padding: 15px 50px;
}